import * as GWU from 'gw-utils';
import * as Style from '../style';
Style.defaultStyle.add('*', {
    fg: 'white',
    bg: -1,
    align: 'left',
    valign: 'top',
});
export class Widget {
    constructor(term, opts = {}) {
        this.tag = 'text';
        this.bounds = new GWU.xy.Bounds(0, 0, 0, 1);
        this.depth = 0;
        this.events = {};
        this.action = '';
        this._style = new Style.Style();
        this.parent = null;
        this.classes = [];
        this._props = {};
        this._attrs = {};
        this.term = term;
        // this.bounds.x = term.x;
        // this.bounds.y = term.y;
        this.bounds.x = opts.x || 0;
        this.bounds.y = opts.y || 0;
        this.bounds.width = opts.width || 0;
        this.bounds.height = opts.height || 1;
        if (opts.tag) {
            this.tag = opts.tag;
        }
        if (opts.id) {
            this.attr('id', opts.id);
        }
        if (opts.parent) {
            this.parent = opts.parent;
        }
        if (opts.depth) {
            this.depth = opts.depth;
        }
        this._style.set(opts);
        if (opts.class) {
            if (typeof opts.class === 'string') {
                opts.class = opts.class.split(/ +/g);
            }
            this.classes = opts.class.map((c) => c.trim());
        }
        if (opts.tabStop) {
            this.prop('tabStop', true);
        }
        if (opts.action) {
            this.attr('action', opts.action);
        }
        if (opts.action) {
            this.action = opts.action;
            this.on('click', (_n, w, e) => {
                if (this.action) {
                    this._bubbleEvent(this.action, w, e);
                }
                return false; // keep bubbling
            });
        }
        this._updateStyle();
    }
    attr(name, v) {
        if (v === undefined)
            return this._attrs[name];
        this._attrs[name] = v;
        return this;
    }
    prop(name, v) {
        if (v === undefined)
            return this._props[name];
        const current = this._props[name];
        if (current !== v) {
            this._props[name] = v;
            // console.log(`${this.tag}.${name}=${v}`);
            this._updateStyle();
        }
        return this;
    }
    toggleProp(name) {
        const current = !!this._props[name];
        this.prop(name, !current);
        return this;
    }
    incProp(name) {
        let current = this.prop(name) || 0;
        if (typeof current === 'boolean') {
            current = current ? 1 : 0;
        }
        else if (typeof current === 'string') {
            current = Number.parseInt(current) || 0;
        }
        ++current;
        this.prop(name, current);
        return this;
    }
    contains(...args) {
        return this.bounds.contains(args[0], args[1]);
    }
    style(opts) {
        if (opts === undefined)
            return this._style;
        this._style.set(opts);
        this._updateStyle();
        return this;
    }
    addClass(c) {
        const all = c.split(/ +/g);
        all.forEach((a) => {
            if (this.classes.includes(a))
                return;
            this.classes.push(a);
        });
        return this;
    }
    removeClass(c) {
        const all = c.split(/ +/g);
        all.forEach((a) => {
            GWU.arrayDelete(this.classes, a);
        });
        return this;
    }
    hasClass(c) {
        const all = c.split(/ +/g);
        return GWU.arrayIncludesAll(this.classes, all);
    }
    toggleClass(c) {
        const all = c.split(/ +/g);
        all.forEach((a) => {
            if (this.classes.includes(a)) {
                GWU.arrayDelete(this.classes, a);
            }
            else {
                this.classes.push(a);
            }
        });
        return this;
    }
    get focused() {
        return !!this.prop('focus');
    }
    set focused(v) {
        this.prop('focus', v);
    }
    get hovered() {
        return !!this.prop('hover');
    }
    set hovered(v) {
        this.prop('hover', v);
    }
    get hidden() {
        let current = this;
        while (current) {
            if (current.prop('hidden'))
                return true;
            current = current.parent;
        }
        return false;
    }
    set hidden(v) {
        this.prop('hidden', v);
    }
    _updateStyle() {
        this._used = this.term.styles.computeFor(this);
        this.term.needsDraw = true; // changed style or state
    }
    draw(buffer) {
        if (this.hidden)
            return false;
        return this._draw(buffer);
    }
    _draw(buffer) {
        this._drawFill(buffer);
        return true;
    }
    _drawFill(buffer) {
        if (this._used.bg !== undefined && this._used.bg !== -1) {
            buffer.fillRect(this.bounds.x, this.bounds.y, this.bounds.width, this.bounds.height, ' ', this._used.bg, this._used.bg);
            return true;
        }
        return false;
    }
    // Events
    mouseenter(e) {
        if (!this.contains(e))
            return;
        if (this.hovered)
            return;
        this.hovered = true;
        this._fireEvent('mouseenter', this, e);
        if (this.parent) {
            this.parent.mouseenter(e);
        }
    }
    mousemove(e) {
        if (this.hidden)
            return false;
        if (this.contains(e) && !e.defaultPrevented) {
            this.mouseenter(e);
            this._fireEvent('mousemove', this, e);
            e.preventDefault();
        }
        else {
            this.mouseleave(e);
        }
        return false;
    }
    mouseleave(e) {
        if (this.contains(e))
            return;
        if (!this.hovered)
            return;
        this.hovered = false;
        this._fireEvent('mouseleave', this, e);
        if (this.parent) {
            this.parent.mouseleave(e);
        }
    }
    click(e) {
        if (this.hidden)
            return false;
        return this._bubbleEvent('click', this, e);
    }
    on(event, cb) {
        let handlers = this.events[event];
        if (!handlers) {
            handlers = this.events[event] = [];
        }
        if (!handlers.includes(cb)) {
            handlers.push(cb);
        }
        return this;
    }
    off(event, cb) {
        let handlers = this.events[event];
        if (!handlers)
            return this;
        if (cb) {
            GWU.arrayDelete(handlers, cb);
        }
        else {
            handlers.length = 0; // clear all handlers
        }
        return this;
    }
    _fireEvent(name, source, e) {
        if (!e || !e.type) {
            e = GWU.io.makeCustomEvent(name, e);
        }
        const handlers = this.events[name] || [];
        let handled = handlers.reduce((out, h) => h(name, source || this, e) || out, false);
        return handled;
    }
    _bubbleEvent(name, source, e) {
        let current = this;
        while (current) {
            if (current._fireEvent(name, source, e))
                return true;
            current = current.parent;
        }
        return this.term.fireEvent(name, source, e);
    }
}
export class WidgetGroup extends Widget {
    constructor(term, opts = {}) {
        super(term, opts);
        this.children = [];
    }
    // contains(e: GWU.xy.XY): boolean;
    // contains(x: number, y: number): boolean;
    // contains(...args: any[]): boolean {
    //     return this.children.some((w) => w.contains(args[0], args[1]));
    // }
    // widgetAt(e: GWU.xy.XY): Widget | null;
    // widgetAt(x: number, y: number): Widget | null;
    // widgetAt(...args: any[]): Widget | null {
    //     return this.children.find((w) => w.contains(args[0], args[1])) || null;
    // }
    // _updateStyle() {
    //     super._updateStyle();
    //     if (this.children) {
    //         this.children.forEach((c) => c._updateStyle());
    //     }
    // }
    addChild(w, beforeIndex = -1) {
        if (w.parent && w.parent !== this)
            throw new Error('Trying to add child that already has a parent.');
        if (!this.children.includes(w)) {
            if (beforeIndex < 0 || beforeIndex >= this.children.length) {
                this.children.push(w);
            }
            else {
                this.children.splice(beforeIndex, 0, w);
            }
        }
        w.parent = this;
        return this;
    }
    removeChild(w) {
        if (!w.parent || w.parent !== this)
            throw new Error('Removing child that does not have this widget as parent.');
        GWU.arrayDelete(this.children, w);
        w.parent = null;
        return this;
    }
}
