import * as GWU from 'gw-utils';
export class Grid {
    constructor(x, y) {
        this._left = 0;
        this._top = 0;
        this._colWidths = [];
        this._rowHeights = [];
        this._col = 0;
        this._row = -1;
        this.x = 0;
        this.y = 0;
        this._left = x;
        this._top = y;
        this.x = x;
        this.y = y;
    }
    cols(...args) {
        if (args.length === 0)
            return this._colWidths;
        if (args.length == 2) {
            args[0] = new Array(args[0]).fill(args[1]);
        }
        if (Array.isArray(args[0])) {
            this._colWidths = args[0];
        }
        return this;
    }
    rows(...args) {
        if (args.length === 0)
            return this._rowHeights;
        if (typeof args[0] === 'number') {
            args[0] = new Array(args[0]).fill(args[1] || 1);
        }
        if (Array.isArray(args[0])) {
            this._rowHeights = args[0];
        }
        return this;
    }
    col(n) {
        if (n === undefined)
            n = this._col;
        this._col = GWU.clamp(n, 0, this._colWidths.length - 1);
        return this._resetX()._resetY(); // move back to top of our current row
    }
    nextCol() {
        return this.col(this._col + 1);
    }
    row(n) {
        if (n === undefined)
            n = this._row;
        this._row = GWU.clamp(n, 0, this._rowHeights.length - 1);
        return this._resetY()._resetX(); // move back to beginning of current column
    }
    nextRow() {
        return this.row(this._row + 1).col(0);
    }
    setRowHeight(h) {
        if (h < 0)
            return this;
        this._rowHeights[this._row] = h;
        return this;
    }
    _resetX() {
        this.x = this._left;
        for (let i = 0; i < this._col; ++i) {
            this.x += this._colWidths[i];
        }
        return this;
    }
    _resetY() {
        this.y = this._top;
        for (let i = 0; i < this._row; ++i) {
            this.y += this._rowHeights[i];
        }
        return this;
    }
}
